
.fullBodyBG {
  height: 100vh;
  background-color: black;
  /* background: -webkit-linear-gradient(to right, #28313b , #28313b); 

 background: linear-gradient(to right,  #28313b , #28313b); */

}

  
  
  ::-webkit-scrollbar {
    width: 12px;
    height: 10px;
  }
  ::-webkit-scrollbar-track {
    background-color: transparent;
  }
  
  
  ::-webkit-scrollbar-thumb {
    background-color: #c5c5c5;
    border-radius: 20px;
    border: 2px solid transparent;
    background-clip: content-box;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background-color: #7c7c7c;
  }
/* The Modal (background) */
.modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  }
  
  /* Modal Content/Box */
  .modal-content {
    background-color: rgba(0, 0, 0, 0.87);
    margin:15% auto; 
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
    -webkit-animation-name: animatetop;
            animation-name: animatetop;
   -webkit-animation-duration: 0.2s;
           animation-duration: 0.2s;
   box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);

  }
  
  
  /* Add Animation */
  @-webkit-keyframes animatetop {
    from {-webkit-transform: scale(.8,.6);transform: scale(.8,.6); opacity: .2}
    to {-webkit-transform: scale(1,1);transform: scale(1,1); opacity: 1}
  }
  @keyframes animatetop {
    from {-webkit-transform: scale(.8,.6);transform: scale(.8,.6); opacity: .2}
    to {-webkit-transform: scale(1,1);transform: scale(1,1); opacity: 1}
  }


